import React, { Component } from 'react';

import Dashboard from './components/admin/dashboard';
import DashboardSideDrawer from './components/admin/dashboard-side-drawer';
import AdminLayout from './components/admin/AdminLayout';

const BackDrop = props => (
    <div>
        <div onClick={props.click}>
        </div>
        <style jsx>{`
             position: fixed;
             width: 100%;
             height: 100%;
             top: 0;
             left: 0;
             background: rgba(0, 0, 0, 0.3);
             z-index: 100;
        `}</style>
    </div>
)

class AdminDashboard extends Component {
    state = {
        sideDrawerOpen: false
    };
    drawerToggleClickHandler = () => {
        this.setState(prevState => {
            return { sideDrawerOpen: !prevState.sideDrawerOpen };
        });
    };
    backdropClickHandler = () => {
        this.setState({ sideDrawerOpen: false });
    };

    render() {
        let backdrop;
        if (this.state.sideDrawerOpen) {
            backdrop = <BackDrop click={this.backdropClickHandler} />;
        }

        return (
            <div>
                <AdminLayout>
                    <Dashboard drawerClickHandler={this.drawerToggleClickHandler} />
                    <DashboardSideDrawer show={this.state.sideDrawerOpen} click={this.backdropClickHandler} />
                    {backdrop}

                    <style jsx>
                        {`
                .hover:hover {
                    margin-left: 10px;
                    margin-right: -10px;
                    margin-top: -4px;
                    margin-bottom: 4px;
                }
                .tabs_side_drawer{
                    height: 100%;
                    background: white;
                    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
                    position: fixed;
                    top: 1px;
                    bottom: 1px;
                    left: 0;
                    width: 60%;
                    max-width: 400px;
                    z-index: 200;
                    transform: translateX(-100% );
                    transition: transform 0.5s ease-out;
                }
                .tabs_side_drawer.open{
                    transform: translateX(0);
                }
                @media (max-width: 991px) {
                    .tabs {
                      display: none;
                    }
                    .tab_content {
                        display: none;
                    }
                }
                @media (min-width: 992px) {
                    .side_tab_toogle_btn {
                        display: none;
                    }
                    .tabs_side_drawer {
                        display: none;
                    }
                    .tabs_side_drawer_tab_content {
                        display: none;
                    }
                }
            `}
                    </style>
                </AdminLayout>
            </div>
        );
    }
}

export default AdminDashboard;